* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6, ul, p, button {
  margin: 0;
  padding: 0;
}

a {
  color: unset;
  text-decoration: none;
}

li {
  list-style: none;
}

figure, blockquote {
  margin: 0;
}

hr {
  width: 100%;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.info-card {
  background-color: #ffffff26;
  border-radius: 20px;
  flex-flow: column;
  justify-content: start;
  align-items: center;
  gap: 10px 0;
  width: 180px;
  height: 205px;
  padding: 20px 10px 0;
  display: flex;
}

.info-card__icon {
  fill: #000;
  width: 25px;
  height: 25px;
}

.info-card__title {
  color: #000;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
}

.info-card__desc {
  text-align: center;
  color: #000;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
}

.info-card:hover {
  cursor: pointer;
  background-color: #ffffff0d;
}

.info-card:hover .info-card__icon {
  fill: #fff;
}

.info-card:hover .info-card__title, .info-card:hover .info-card__desc {
  color: #fff;
}

@media (width <= 428px) {
  .info-card {
    width: 140px;
    height: 170px;
  }

  .info-card__title {
    color: #000;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
  }

  .info-card__desc {
    color: #000;
    font-size: 11px;
    font-style: normal;
    font-weight: 300;
  }
}

.img-card {
  background-color: #ffffff26;
  border-radius: 10px;
  flex-flow: column;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  gap: 0;
  width: 325px;
  height: 295px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.img-card__title {
  color: #fff;
  flex-flow: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 0;
  font-size: 23px;
  font-style: normal;
  font-weight: 400;
  display: flex;
}

.img-card__preview {
  width: 100%;
  height: 220px;
}

.img-card__btn {
  z-index: 1;
  background-color: #fff;
  border: none;
  border-radius: 20px;
  width: 120px;
  height: 90px;
  transition: rotate .2s;
  position: absolute;
  bottom: -20px;
  left: -30px;
  rotate: 7deg;
}

.img-card__btn-icon {
  fill: #000;
  width: 36px;
  height: 23px;
  translate: 12px -3px;
  rotate: -7deg;
}

.img-card__btn:hover {
  cursor: pointer;
  rotate: 7deg;
  scale: 1.3;
}

.img-card__btn:hover .img-card__btn-icon {
  translate: 17px -8px;
  rotate: -7deg;
}

.img-card:hover {
  cursor: pointer;
  box-shadow: 0 8px 20px 12px #00000026, inset 0 -5px 70px 25px #0003;
}

@media (width <= 428px) {
  .img-card {
    width: 300px;
  }
}

.wave-card {
  background: url("wave_card_bg.db1ea54d.svg") center / contain no-repeat;
  flex-flow: column;
  justify-content: start;
  align-items: start;
  gap: 35px 0;
  width: 500px;
  height: 275px;
  display: flex;
}

.wave-card__avatar {
  margin: 20px 0 0 20px;
}

.wave-card__quote {
  color: #fff;
  text-align: justify;
  margin: 0 45px 0 30px;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4;
}

@media (width <= 768px) {
  .wave-card {
    background: url("wave_card_bg.db1ea54d.svg") 50% 0 / contain no-repeat;
    row-gap: 15px;
    width: 330px;
  }

  .wave-card__quote {
    color: #fff;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
  }
}

.footer-section .footer__terms-link:hover, .header .nav__title-link:hover, .header .nav__links .list__item-link:hover, .header .nav .covid__link:hover {
  cursor: pointer;
  color: #000;
}

.hero__notify-btn:hover {
  cursor: pointer;
  color: #fff;
}

.header .nav .globe__link-icon:hover, .mobile-navigation__burger-icon:hover, .mobile-navigation .nav__close-icon:hover {
  cursor: pointer;
  stroke: #000;
}

.header {
  background-color: #ffffff26;
  border-bottom: 1px solid #fff;
  width: 100%;
  height: 50px;
}

.header .nav {
  flex-flow: row;
  justify-content: start;
  align-items: center;
  gap: 0;
  height: 50px;
  display: flex;
}

.header .nav__title {
  border-right: 1px solid #fff;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 255px;
  height: 100%;
  display: flex;
}

.header .nav__title-link {
  color: #fff;
  font-size: 25px;
  font-weight: 500;
}

.header .nav__title-link span {
  font-weight: 700;
}

.header .nav__links {
  flex-grow: 1;
}

.header .nav__links .list {
  flex-flow: row;
  justify-content: start;
  align-items: center;
  gap: 0 min(100px, 5vw);
  padding: 0 0 0 min(100px, 5vw);
  display: flex;
}

.header .nav__links .list__item-link {
  color: #fff;
  font-size: 22px;
  font-weight: 400;
}

@media (width <= 992px) {
  .header .nav__links .list {
    flex-flow: row;
    justify-content: center;
    align-items: center;
    gap: 0 min(100px, 5vw);
    padding: 0;
    display: flex;
  }
}

.header .nav .covid {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  height: 100%;
  padding: 0 30px;
  display: flex;
}

.header .nav .covid__link {
  color: #fff;
  font-size: 16px;
  font-weight: 700;
}

.header .nav .globe {
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 90px;
  display: flex;
}

.header .nav .globe__link-icon {
  width: 27px;
  height: 28px;
}

@media (width <= 768px) {
  .header .nav__links, .header .nav .covid, .header .nav .globe {
    display: none;
  }
}

.mobile-navigation {
  display: none;
}

.mobile-navigation__burger {
  z-index: 10;
  position: absolute;
  top: 13px;
  right: 20px;
}

.mobile-navigation__burger-icon {
  stroke: #fff;
  width: 33px;
  height: 22px;
}

.mobile-navigation__toggler {
  display: none;
}

.mobile-navigation__toggler:checked + .nav {
  translate: 0;
}

.mobile-navigation .nav {
  z-index: 999;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: #00000080;
  padding: 100px 70px 0;
  transition: translate .22s;
  position: fixed;
  inset: 0;
  translate: 100%;
}

.mobile-navigation .nav__list {
  flex-flow: column;
  justify-content: start;
  align-items: start;
  gap: 20px 0;
  display: flex;
}

.mobile-navigation .nav__list-item-link {
  color: #fff;
  text-shadow: 2px 2px 5px #000;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
}

.mobile-navigation .nav__close {
  z-index: 10;
  position: absolute;
  top: 18px;
  right: 23px;
}

.mobile-navigation .nav__close-icon {
  stroke: #fff;
  width: 25px;
  height: 22px;
}

@media (width <= 768px) {
  .mobile-navigation {
    display: block;
  }
}

.hero {
  background: url("cloud_alpha.c81e7aa9.png") center / contain no-repeat;
  max-width: 1260px;
  height: 830px;
  margin: 0 auto;
  padding: 140px 0 0;
  position: relative;
}

.hero__content {
  width: 900px;
