  font-size: 110px;
  font-style: normal;
  font-weight: 700;
  line-height: 1;
}

.hero__content .hero__action-btn {
  cursor: pointer;
  color: #fff;
  z-index: 1;
  background-color: #000;
  border: none;
  border-radius: 45px;
  width: 380px;
  height: 80px;
  font-size: 28px;
  font-style: normal;
  font-weight: 500;
  position: absolute;
  bottom: 15px;
  right: 0;
}

.hero__content .hero__action-btn:hover {
  background-color: #000000a6;
}

.hero__content .hero__action-btn:focus {
