.hero__notify {
  background-color: #ffffff26;
  border-radius: 20px;
  flex-flow: row;
  justify-content: end;
  align-items: center;
  gap: 0 10px;
  width: 680px;
  height: 60px;
  margin: 170px auto 0;
  display: flex;
  position: relative;
}

.hero__notify-img {
  z-index: 1;
  width: 126px;
  height: 198px;
  position: absolute;
  top: -75px;
  left: -35px;
}

.hero__notify-content {
  text-transform: uppercase;
  color: #000;
  border-right: 1px solid #fff;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 450px;
  height: 100%;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  display: flex;
}

.hero__notify-btn {
  text-transform: uppercase;
  color: #000;
  background: none;
  border: none;
  width: 115px;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
}

.hero__notify-btn:focus {
  outline: none;
}

.hero__notify-btn:focus-visible {
  border-radius: 5px;
  outline: 2px solid #fff;
}

@media (width <= 768px) {
  .hero__notify {
    flex-flow: column;
    justify-content: start;
    align-items: center;
    gap: 0;
    width: 100%;
    max-width: 450px;
    height: -moz-fit-content;
    height: fit-content;
    margin: 140px auto 0;
    display: flex;
  }

  .hero__notify-content {
    color: #000;
    border-bottom: 1px solid #fff;
    border-right: none;
    width: 100%;
    padding: 15px 10px 15px 90px;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.3;
  }

  .hero__notify-btn {
    color: #000;
    padding: 15px 0;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
  }
}

@media (width <= 576px) {
  .hero__notify-img {
    z-index: 1;
    position: absolute;
    top: -45px;
    left: -15px;
  }

  .hero__notify-content {
    color: #000;
    padding: 10px 10px 10px 100px;
    font-size: 11px;
    font-style: normal;
    font-weight: 400;
  }
}

.hero__scroll {
  z-index: 1;
  position: absolute;
  bottom: 200px;
  left: 10px;
}

.hero__scroll-content {
  color: #fff;
  writing-mode: vertical-rl;
  cursor: pointer;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  position: relative;
}

.hero__scroll-content:after {
  content: "";
  z-index: 1;
  background-color: #fff;
  width: 1px;
  height: 75px;
  display: block;
  position: absolute;
  top: 100px;
  left: 8px;
}

@media (width <= 992px) {
  .hero__scroll {
    z-index: 1;
    position: absolute;
    bottom: 130px;
    left: 10px;
  }
}

@media (width <= 576px) {
  .hero__scroll {
    z-index: 1;
    position: absolute;
    bottom: 200px;
    left: 10px;
  }
}

@media (width <= 768px) {
  .hero {
    background: url("cloud_alpha.c81e7aa9.png") 0 25% / contain no-repeat;
    padding: 110px 15px 0;
  }
}

@media (width <= 576px) {
  .hero {
    height: 700px;
  }
}

.work-section {
  padding: 50px 20px 180px;
}

.work-section .work {
