  height: 41px;
}

@media (width <= 768px) {
  .work-section .work__cards-wrapper {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: "analysis design"
                         "dev maintenance"
                         "testing integration";
  }

  .work-section .work__cards-wrapper .card--analysis, .work-section .work__cards-wrapper .card--design, .work-section .work__cards-wrapper .card--dev, .work-section .work__cards-wrapper .card--maintenance, .work-section .work__cards-wrapper .card--testing, .work-section .work__cards-wrapper .card--integration {
